@font-face {
    font-family: 'DINPro';
    src: url('/assets/fonts/DINPro-Regular.eot');
    src: url('/assets/fonts/DINPro-Regular.eot?#iefix') format('embedded-opentype'),
         url('/assets/fonts/DINPro-Regular.otf')  format('opentype'),
         url('/assets/fonts/DINPro-Regular.woff') format('woff'),
         url('/assets/fonts/DINPro-Regular.ttf')  format('truetype'),
         url('/assets/fonts/DINPro-Regular.svg#DINPro-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'DINPro';
    src: url('/assets/fonts/DINPro-Medium.eot');
    src: url('/assets/fonts/DINPro-Medium.eot?#iefix') format('embedded-opentype'),
         url('/assets/fonts/DINPro-Medium.otf')  format('opentype'),
         url('/assets/fonts/DINPro-Medium.woff') format('woff'),
         url('/assets/fonts/DINPro-Medium.ttf')  format('truetype'),
         url('/assets/fonts/DINPro-Medium.svg#DINPro-Medium') format('svg');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Gotham Medium';
    src: url('/assets/fonts/Gotham-Medium.ttf')  format('truetype');
}

@font-face {
    font-family: 'Gotham Rounded Book';
    src: url('/assets/fonts/Gotham-Rounded-Book.otf')  format('opentype');
}

@font-face {
    font-family: 'Gotham Rounded Light';
    src: url('/assets/fonts/Gotham-Rounded-Light.otf')  format('opentype');
}

@font-face {
    font-family: 'Gotham Rounded Medium';
    src: url('/assets/fonts/Gotham-Rounded-Medium.otf')  format('opentype');
}
