@keyframes toRightFromLeft {
  49% {
    transform: translateX(100%); }
  50% {
    opacity: 0;
    transform: translateX(-100%); }
  51% {
    opacity: 1; } }

@keyframes toRightFromLeftVerticallyCentered {
  49% {
    transform: translate(100%, -50%); }
  50% {
    opacity: 0;
    transform: translate(-100%, -50%); }
  51% {
    opacity: 1; } }

.feature-tower {
  border-radius: 5px;
  color: #ffffff;
  display: inline-block;
  overflow: hidden;
  padding: 20px;
  text-align: center;
  transition: background-color 0.3s;
  vertical-align: top;
  width: 20%; }
  .feature-tower:hover {
    background-color: #ffffff;
    color: #ff0000;
    cursor: pointer; }
    .feature-tower:hover .feature-tower--content {
      animation: toRightFromLeft 0.3s forwards; }
    .feature-tower:hover .feature-tower--button {
      border-color: #ff0000;
      color: #ff0000; }
    .feature-tower:hover .feature-tower--adsl-senior {
      background: url("../../../assets/img/icons/adsl-senior-hover.png") no-repeat center; }
    .feature-tower:hover .feature-tower--adsl-office {
      background: url("../../assets/img/icons/adsl-office-hover.png") no-repeat center; }
    .feature-tower:hover .feature-tower--dedicated {
      background: url("../../assets/img/icons/dedicated-hover.png") no-repeat center; }
    .feature-tower:hover .feature-tower--wireless {
      background: url("../../assets/img/icons/wireless-hover.png") no-repeat center; }
    .feature-tower:hover .feature-tower--hosting {
      background: url("../../assets/img/icons/hosting-hover.png") no-repeat center; }
  .feature-tower--adsl-senior {
    background: url("../../assets/img/icons/adsl-senior.png") no-repeat center; }
    .feature-tower--adsl-senior_big {
      background: url("../../../assets/img/icons/adsl-senior-big.png") no-repeat center; }
  .feature-tower--adsl-office {
    background: url("../../assets/img/icons/adsl-office.png") no-repeat center; }
    .feature-tower--adsl-office_big {
      background: url("../../../assets/img/icons/adsl-office-big.png") no-repeat center; }
  .feature-tower--dedicated {
    background: url("../../assets/img/icons/dedicated.png") no-repeat center; }
    .feature-tower--dedicated_big {
      background: url("../../../assets/img/icons/dedicated-big.png") no-repeat center; }
  .feature-tower--wireless {
    background: url("../../assets/img/icons/wireless.png") no-repeat center; }
    .feature-tower--wireless_big {
      background: url("../../../assets/img/icons/wireless-big.png") no-repeat center; }
  .feature-tower--hosting {
    background: url("../../assets/img/icons/hosting.png") no-repeat center; }
    .feature-tower--hosting_big {
      background: url("../../../assets/img/icons/hosting-big.png") no-repeat center; }
  .feature-tower--icon {
    height: 64px;
    margin: 0 auto 5px auto;
    width: 64px; }
  .feature-tower--description {
    font-family: 'Gotham Medium';
    font-size: 18px;
    line-height: 140%;
    margin: 15px 0 20px;
    min-height: 65px; }
  .feature-tower--button {
    transition: background-color 0.3s; }
  @media screen and (max-width: 1024px) {
    .feature-tower {
      display: block;
      margin: 0 auto;
      width: 90%; } }

