.form-tower {
  display: block;
  margin: 0 auto;
  overflow: hidden;
  padding-top: 20px;
  width: 750px; }
  .form-tower .fieldset {
    overflow: hidden;
    margin-bottom: 20px;
    width: 100%; }
    .form-tower .fieldset-label {
      box-sizing: border-box;
      color: #ffffff;
      float: left;
      min-width: 30%;
      padding: 8px 30px 0;
      text-align: right; }
    .form-tower .fieldset-input {
      float: left;
      min-width: 70%; }
      .form-tower .fieldset-input .input {
        border: 2px solid #ffffff;
        background-color: transparent;
        color: #ffffff;
        padding: 6px 15px;
        width: 100%; }
    .form-tower .fieldset_right-align {
      float: right;
      margin-left: 20px;
      width: auto; }
    .form-tower .fieldset_success {
      color: #404040;
