@keyframes toRightFromLeft {
  49% {
    transform: translateX(100%); }
  50% {
    opacity: 0;
    transform: translateX(-100%); }
  51% {
    opacity: 1; } }

@keyframes toRightFromLeftVerticallyCentered {
  49% {
    transform: translate(100%, -50%); }
  50% {
    opacity: 0;
    transform: translate(-100%, -50%); }
  51% {
    opacity: 1; } }

.feature {
  border-radius: 5px;
  color: #6a7784;
  display: inline-block;
  overflow: hidden;
  padding: 20px;
  text-align: center;
  transition: background-color 0.3s;
  vertical-align: top;
  width: 25%; }
  .feature:hover {
    background-color: #4dceaf;
    color: #e3eaf0;
    cursor: pointer; }
    .feature:hover .feature--content {
      animation: toRightFromLeft 0.3s forwards; }
    .feature:hover .feature--button {
      background-color: #e3eaf0; }
    .feature:hover .feature--buildings {
      background-image: url("../../assets/img/icons/buildings-hover.png"); }
    .feature:hover .feature--engineering {
      background-image: url("../../assets/img/icons/engineering-hover.png"); }
    .feature:hover .feature--maintenance {
      background-image: url("../../assets/img/icons/maintenance-hover.png"); }
    .feature:hover .feature--networking {
      background-image: url("../../assets/img/icons/networking-hover.png"); }
  .feature--buildings {
    background: url("../../assets/img/icons/buildings.png") no-repeat center; }
  .feature--engineering {
    background: url("../../assets/img/icons/engineering.png") no-repeat center; }
  .feature--maintenance {
    background: url("../../assets/img/icons/maintenance.png") no-repeat center; }
  .feature--networking {
    background: url("../../assets/img/icons/networking.png") no-repeat center; }
  .feature--icon {
    height: 64px;
    margin: 0 auto 5px auto;
    width: 64px; }
  .feature--title {
    font-size: 18px;
    font-weight: normal;
    margin: 0;
    text-transform: uppercase; }
  .feature--description {
    font-size: 16px;
    line-height: 140%;
    margin: 15px 0 20px;
    min-height: 65px; }
  .feature--button {
    transition: background-color 0.3s; }
  @media screen and (max-width: 1024px) {
    .feature {
      display: block;
      margin: 0 auto;
      width: 90%; } }

.section-details--title {
  border-top: 1px solid #4eace9;
  text-align: center; }
  .section-details--title-text {
    background-color: #e3eaf0;
    color: #6a7784;
    display: inline-block;
    font-weight: normal;
    margin: 0;
    padding: 0 20px;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    top: -15px; }

.sub-section {
  display: inline-block;
  vertical-align: top;
  width: 33.333333%; }
  .sub-section_spaced {
    padding: 0 2%; }
  .sub-section--title {
    border: 1px solid #4eace9;
    color: #4eace9;
    font-weight: normal;
    min-height: 88px;
    overflow: hidden;
    padding: 20px;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    transition: background-color 0.3s; }
    .sub-section--title_hover {
      background-color: #4eace9;
      color: #e3eaf0;
      cursor: pointer; }
    .sub-section--title-text {
      display: inline-block;
      left: 0;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 100%; }
      .sub-section--title-text_hover {
        animation: toRightFromLeftVerticallyCentered 0.3s forwards; }
  .sub-section-separator {
