@keyframes toRightFromLeft {
  49% {
    transform: translateX(100%);
  }

  50% {
    opacity: 0;
    transform: translateX(-100%);
  }

  51% {
    opacity: 1;
  }
}

@keyframes toRightFromLeftVerticallyCentered {
  49% {
    transform: translate(100%, -50%);
  }

  50% {
    opacity: 0;
    transform: translate(-100%, -50%);
  }

  51% {
    opacity: 1;
  }
}