@keyframes toRightFromLeft {
  49% {
    transform: translateX(100%); }
  50% {
    opacity: 0;
    transform: translateX(-100%); }
  51% {
    opacity: 1; } }

@keyframes toRightFromLeftVerticallyCentered {
  49% {
    transform: translate(100%, -50%); }
  50% {
    opacity: 0;
    transform: translate(-100%, -50%); }
  51% {
    opacity: 1; } }

.primary-navigation {
  margin: 0 auto;
  max-width: 1300px;
  padding: 12px 80px;
  width: 100%; }
  .primary-navigation--logo {
    display: inline-block;
    vertical-align: middle;
    width: 15%; }
    @media screen and (max-width: 1024px) {
      .primary-navigation--logo {
        display: block;
        text-align: center;
        width: 100%; } }
    .primary-navigation--logo_tower {
      color: #ffffff;
      font-family: 'Times New Roman';
      font-size: 35px;
      font-weight: lighter; }
  .primary-navigation--menu {
    display: inline-block;
    list-style-type: none;
    margin: 0;
    text-align: right;
    vertical-align: middle;
    width: 84.4%; }
    .primary-navigation--menu-item {
      display: inline-block;
      margin-left: 34px;
      text-transform: uppercase; }
      .primary-navigation--menu-item:first-child {
        margin-left: 0; }
      .primary-navigation--menu-item-link {
        color: #6a7784;
        font-size: 14px;
        text-decoration: none; }
        .primary-navigation--menu-item-link_active {
          color: #009984;
          font-weight: bold; }
      @media screen and (max-width: 1024px) {
        .primary-navigation--menu-item {
          margin-left: 15px; } }
      .primary-navigation--menu-item img {
        -webkit-transition: all .2s ease-in;
        transition: all .2s ease-in;
        -webkit-filter: grayscale(100%);
        filter: grayscale(100%);
        opacity: .75; }
        .primary-navigation--menu-item img:hover {
          -webkit-filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.4)) grayscale(0%);
          filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.4)) grayscale(0%);
          opacity: 1; }
    @media screen and (max-width: 1024px) {
      .primary-navigation--menu {
        display: block;
        text-align: center;
        width: 100%;
        padding: 0; } }
  @media screen and (max-width: 768px) {
    .primary-navigation {
      padding: 12px 20px; } }

.secondary-navigation {
  text-align: right; }
  .secondary-navigation--menu {
